// @flow 

import React, { Component } from 'react';
import {
  isToday,
  isPastDate,
  isFutureDate,
  formatCentsToDollars } from 'clark-utils';
import type { BillsType } from '../../types'
import './Totals.css';

type Props = {
  bills: BillsType,
};

export default class Totals extends Component<Props> {
  static defaultProps = {
    bills: {}
  }

  calculateTotal = (bills: BillsType): string => {
    let total = 0;

    for (let key in bills) {
      const bill = bills[key];
      if (bill) {
        total += bills[key]['amountInCents'];
      }
    }

    return total > 0 ? formatCentsToDollars(total) : total.toString();
  }

  calculatePaid = (bills: BillsType): string => {
    let total = 0;
    
    for (let key in bills) {
      const bill = bills[key];
      if (bill['status'] === 'paid') {
        total += bills[key]['amountInCents'];
      }
    }

    return total > 0 ? formatCentsToDollars(total) : total.toString();
  }

  calculateOverdue = (bills: BillsType): string => {
    let total = 0;

    for (let key in bills) {
      const bill = bills[key];
      const billDue = bill['dueDate'];
      if (bill['status'] === 'pending' && isPastDate(billDue)) {
        total += bills[key]['amountInCents'];
      }
    }

    return total > 0 ? formatCentsToDollars(total) : total.toString();
  }

  calculateOutstanding = (bills: BillsType): string => {
    let total = 0;

    for (let key in bills) {
      const bill = bills[key];
      const billDue = bill['dueDate'];
      if (bill['status'] === 'pending' && (isToday(billDue) || isFutureDate(billDue))) {
        total += bills[key]['amountInCents'];
      }
    }

    return total > 0 ? formatCentsToDollars(total) : total.toString();
  }

  render() {
      const bills = this.props.bills;
      const total = this.calculateTotal(bills)
      const paid = this.calculatePaid(bills)
      const overdue = this.calculateOverdue(bills)
      const outstanding = this.calculateOutstanding(bills)

    return (
      <div className="totals-wrapper">
        <p className="all-total">Total: <span className="number-total">{total}</span></p>
        <p className="paid-total success">Paid: <span className="number-total">{paid}</span></p>
        <p className="overdue-total danger">Overdue: <span className="number-total">{overdue}</span></p>
        <p className="outstanding-total pending">Outstanding: <span className="number-total">{outstanding}</span></p>
      </div>
    )
  }
}



